<template>
  <div class="profile">
    <el-card class="box-card">
      <template>
        <div class="demo-type">
          <div>
            <el-avatar :size="80">
              <img src="@/assets/avator.jpeg" alt="" />
            </el-avatar>
          </div>
        </div>
      </template>
      <template>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="date" label="登录名称"> </el-table-column>
          <el-table-column prop="name" label="admin"> </el-table-column>
        </el-table>
      </template>
    </el-card>

    <el-tabs type="border-card" class="border-card">
      <el-tab-pane label="基本资料">
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          class="demo-ruleForm"
        >
          <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
            <el-form-item label="用户名称:">
              <el-input v-model="formLabelAlign.name"></el-input>
            </el-form-item>
            <el-form-item label="手机号码:">
              <el-input v-model="formLabelAlign.phone"></el-input>
            </el-form-item>
            <el-form-item label="邮箱:">
              <el-input v-model="formLabelAlign.emil"></el-input>
            </el-form-item>
            <el-form-item label="性别:">
              <template>
                <el-radio v-model="radio" label="1">男</el-radio>
                <el-radio v-model="radio" label="2">女</el-radio>
              </template>
            </el-form-item>
          </el-form>
          <el-form-item class="profileBut">
            <el-button type="primary" @click="submitForm('ruleForm')">保存 </el-button>
            <el-button @clusernaick="resetForm('ruleForm')">关闭</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane label="修改密码">
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="旧密码" prop="oldpass">
            <el-input
              type="password"
              v-model="ruleForm.oldpass"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop="newPass">
            <el-input
              type="password"
              v-model="ruleForm.newPass"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input
              type="password"
              v-model="ruleForm.checkPass"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"
              >保存</el-button
            >
            <el-button @click="resetForm('ruleForm')">关闭</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass1 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("oldPass");
        }
        callback();
      }
    };

    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass3 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      tableData: [
        {
          date: "手机号",
          name: "16692845708",
        },
        {
          date: "所属岗位",
          name: "总经理",
        },
        {
          date: "邮箱",
          name: "2230692954@qq.com",
        },
        {
          date: "创建时间",
          name: "2022/2/2",
        },
      ],
       formLabelAlign: {
          name: 'admin',
          phone: '13891389020',
          emil: '2309873018@qq.com'
        },
      labelPosition: 'right',
      radio: "1",
      ruleForm: {
        oldpass: "",
        newPass: "",
        checkPass: "",
      },
      rules: {
        oldpass: [{ validator: validatePass1, trigger: "blur" }],
        newPass: [{ validator: validatePass2, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style scoped>
.profile{
  display: flex;
  justify-content: space-between;
}
.box-card {
  width: 38%;
  border-radius: 6px;
}
.demo-type {
  margin: 10px 0;
}
#cardMessage {
  margin-left: 0;
}
.border-card {
  width:60%;
  border-radius: 6px;
}
.profileBut{
  margin-left: 50px;
}
</style>

